html {
  background-color: lightsalmon; }

html, body {
  height: 100%; }

ul {
  list-style-type: none; }

a {
  text-decoration: none; }

.site-content {
  padding: var(--spacing-site, 2em); }

.site-content {
  max-width: 60rem;
  margin: 0 auto; }

.site-header {
  background-color: orangered;
  color: white; }

body,
.site {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  min-height: 100vh;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column; }

.site-content {
  -webkit-box-flex: 1;
  -ms-flex: 1 0 auto;
  flex: 1 0 auto; }

.site-footer {
  bottom: 0;
  background-color: black;
  color: white;
  margin-top: 2rem; }

.navigation ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
  justify-content: space-around; }

.navigation ul > li > a {
  color: white; }

.site-footer ul > li > a {
  color: white; }

.site-content ul li a {
  color: black; }

.site-content a {
  color: black; }

/*Whitespace reset*/
* {
  padding: 0;
  margin: 0; }

html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

*, *:before, *:after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit; }

/*typography*/
html {
  font: var(--font-size-initial, 14px)/1.5 "Verdana", sans-serif; }

@media screen and (min-width: 500px) {
  html {
    font-size: var(--font-size-lv2, 16px); } }

@media screen and (min-width: 700px) {
  html {
    font-size: var(--font-size-lv3, 18px); } }

@media screen and (min-width: 1600px) {
  html {
    font-size: var(--font-size-lv4, 21px); } }

body, table, td, th, textarea, option, select, form, input {
  font: inherit; }

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2; }

h1 {
  font-size: 3.998em; }

h2 {
  font-size: 2.827em; }

h3 {
  font-size: 1.999em; }

h4 {
  font-size: 1.414em; }

p, ul, ol {
  padding-bottom: 1.5em; }

ul, ol {
  padding-left: 1.5rem; }

li {
  list-style-position: inside; }

:root {
  --font-size-initial: 14px;
  --font-size-lv2: 16px;
  --font-size-lv3: 18px;
  --font-size-lv4: 21px; }
